// åˆå§‹åŒ–çŠ¶æ€
let initNavList = {
    navMain: [],
    bookDetails: []
}

export function home(state = initNavList, action) {
    switch (action.type) {
        case 'RECEIVE_NAV':
            return {
                ...state,   //ä¸‰ä¸ªç‚¹æ˜¯å±•å¼€ç¬¦
                navMain: action.navMain
            }
        case 'RECEIVE_BOOK':
            return {
                ...state,
                bookDetails: action.bookDetails
            }
        default:
            return {...state};
    }
}


